
@import url("https://fonts.googleapis.com/css?family=Crimson+Text");
#app {
  font-family: 'Crimson Text', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  max-height: 100%;
  margin: 0;
}
@media (max-width: 768px) {
#app {
      padding-top: 5%;
}
}
body {
  margin: 0;
}
nav {
  position: absolute;
  top: 0px;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 5%;
}
@media (max-width: 768px) {
nav {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -webkit-box-pack: start;
          -ms-flex-pack: start;
              justify-content: flex-start;
      height: 50px;
      width: 100%;
      background-color: rgba(255, 255, 255, 0);
}
}
.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 70%;
  height: 40px;
}
@media (max-width: 768px) {
.wrapper {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      width: 100vw;
      height: 50px;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      margin-top: 10px;
}
}
.links {
  color: #9e9c9c;
  text-decoration: none;
  font-size: 1.5em;
  -webkit-transition: all 400ms;
  transition: all 400ms;
  font-weight: normal;
  position: relative;
}
.links:hover {
    color: #bfa770;
    margin-top: 8px;
}
@media (max-width: 768px) {
.links {
      font-size: 1.1em;
      margin: 2%;
}
.links:hover {
        margin-top: 3%;
        margin-left: 0%;
}
}
.links:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0;
  left: 0;
  background: #b39c4e;
  visibility: hidden;
  border-radius: 5px;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: .18s linear;
  transition: .18s linear;
}
.links:hover:before,
.links:focus:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.menuBtn {
  position: absolute;
  top: 10px;
  left: 20px;
  border: none;
  background: none;
  font-size: 2em;
  -webkit-transition: all 200ms;
  transition: all 200ms;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0;
  margin: 0;
  -webkit-filter: opacity(0.3);
          filter: opacity(0.3);
}
.menuBtn:focus {
    outline: 0;
}
.menuBtn:hover {
    -webkit-filter: opacity(0.7);
            filter: opacity(0.7);
}
.main {
  padding-top: 100px;
}
@media (max-width: 768px) {
.main {
      padding-top: 220px;
}
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.fade2-enter-active, .fade2-leave-active {
  -webkit-transition: opacity 0.6s;
  transition: opacity 0.6s;
}
.fade2-enter, .fade2-leave-active {
  opacity: 0;
}



